<template>
  <div class="foodOrderFilter">
    <foodHeaderNav title="订单筛选" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="order-status-container">
      <div class="order-status-title">订单状态</div>
      <div class="order-status-list">
        <span
          class="order-status-item"
          :class="[activeStatus==-2?'order-status-active':'']"
          @click="statusChoseFun(-2)"
        >全部</span>
        <span
          class="order-status-item"
          :class="[activeStatus==1?'order-status-active':'']"
          @click="statusChoseFun(1)"
        >待支付</span>
        <span
          class="order-status-item"
          :class="[activeStatus==0?'order-status-active':'']"
          @click="statusChoseFun(0)"
        >未下厨</span>
        <span
          class="order-status-item"
          :class="[activeStatus==2?'order-status-active':'']"
          @click="statusChoseFun(2)"
        >已下厨</span>
        <span
          class="order-status-item"
          :class="[activeStatus==-1?'order-status-active':'']"
          @click="statusChoseFun(-1)"
        >失败</span>
      </div>
    </div>
    <div class="order-container">
      <div class="order-title">订单</div>
      <div class="order-content">
        <div class="order-item">
          <div class="order-label">订单日期</div>
          <div class="order-drop-down" @click="openPickerOrderDate()">
            {{ OrderDate }}
            <i class="iconfont icon-ADown drop-icon"></i>
          </div>
          <!-- <i
            class="iconfont icon-quxiao clear-icon"
            v-if="closeOrderDate"
            @click="closeOrderDateFun()"
          ></i>-->
        </div>
      </div>
    </div>
    <div class="order-container">
      <div class="order-title">就餐</div>
      <div class="order-content">
        <div class="order-item">
          <div class="order-label">就餐日期</div>
          <div class="order-drop-down" @click="openPickerEatDate()">
            {{EatDate}}
            <i class="iconfont icon-ADown drop-icon"></i>
          </div>
          <!-- <i class="iconfont icon-quxiao clear-icon" v-if="closeEatDate" @click="closeEatDateFun()"></i> -->
        </div>
        <div class="order-item">
          <div class="order-label">就餐时间</div>
          <div class="order-drop-down" @click="openPickerEatTime()">
            {{EatTime}}
            <i class="iconfont icon-ADown drop-icon"></i>
          </div>
          <!-- <i class="iconfont icon-quxiao clear-icon" v-if="closeEatTime" @click="closeEatTimeFun()"></i> -->
        </div>
      </div>
    </div>
    <div class="foot">
      <div class="cancel foot-btn" @click="resetFun()">重 置</div>
      <div class="confirm foot-btn" @click="okFun()">确 定</div>
    </div>
    <mt-datetime-picker
      v-model="pickerOrderDate"
      type="date"
      ref="pickerOrderDate"
      @confirm="dateOrderFun()"
    ></mt-datetime-picker>
    <mt-datetime-picker
      v-model="pickerEatDate"
      type="date"
      ref="pickerEatDate"
      @confirm="dateEatFun()"
    ></mt-datetime-picker>
    <mt-datetime-picker
      v-model="pickerEatTime"
      type="time"
      ref="pickerOrderTime"
      @confirm="timeEatFun()"
    ></mt-datetime-picker>
  </div>
</template>
<script>
import foodHeaderNav from "./foodHeaderNav";
import moment from "moment";
export default {
  components: {
    foodHeaderNav
  },
  data() {
    return {
      activeStatus: -2,
      closeOrderDate: false,
      closeEatDate: false,
      closeEatTime: false,
      pickerOrderDate: new Date(),
      OrderDate: "",
      pickerEatDate: new Date(),
      EatDate: "",
      pickerEatTime: 0,
      EatTime: ""
    };
  },
  methods: {
    gotoBack() {
      this.$emit("hidePage");
    },
    statusChoseFun(val) {
      this.activeStatus = val;
    },
    openPickerOrderDate() {
      this.$refs.pickerOrderDate.open();
    },
    openPickerEatDate() {
      this.$refs.pickerEatDate.open();
    },
    openPickerEatTime() {
      this.$refs.pickerOrderTime.open();
    },
    dateOrderFun() {
      this.OrderDate = moment(this.pickerOrderDate).format("YYYY-MM-DD");
    },
    dateEatFun() {
      this.EatDate = moment(this.pickerEatDate).format("YYYY-MM-DD");
    },
    timeEatFun() {
      this.EatTime = this.pickerEatTime;
    },
    resetFun() {
      this.activeStatus = -2;
      this.OrderDate = "";
      this.EatDate = "";
      this.EatTime = "";
    },
    okFun() {
      this.$emit("fitlerSet", {
        activeStatus: this.activeStatus,
        OrderDate: this.OrderDate,
        EatTime: this.EatDate + " " + this.EatTime
      });
    }
  }
};
</script>
<style lang="less" scoped>
.foodOrderFilter {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 9;
  width: 100%;
  height: 100vh;
  background-color: #f4f5f7;

  .order-status-container {
    background-color: #fff;
    padding-left: 0.453rem;
    padding-right: 0.453rem;
    text-align: left;
    margin-bottom: 0.267rem;
    .order-status-title {
      height: 1.04rem;
      line-height: 1.04rem;
      font-size: 0.32rem;
      color: #666666;
      border-bottom: 1px solid #eeeeee;
    }
    .order-status-list {
      padding-top: 0.347rem;
      padding-bottom: 0.347rem;
      .order-status-item {
        display: inline-block;
        width: 62px;
        text-align: center;
        height: 0.8rem;
        line-height: 0.8rem;
        background: #f6f8f9;
        font-size: 0.373rem;
        margin-bottom: 0.133rem;
      }
      .order-status-active {
        background: #ff8000;
        color: #ffffff;
      }
    }
  }
  .order-container {
    background-color: #fff;
    padding-left: 0.453rem;
    padding-right: 0.453rem;
    text-align: left;
    margin-bottom: 0.267rem;
    .order-title {
      height: 1.04rem;
      line-height: 1.04rem;
      font-size: 0.32rem;
      color: #666666;
      border-bottom: 1px solid #eeeeee;
    }
    .order-content {
      padding-top: 0.453rem;
      padding-bottom: 0.613rem;

      .order-item {
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 0.213rem;
        text-indent: 0.267rem;
        .order-label {
          color: #333333;
          width: 2.4rem;
        }
        .order-drop-down {
          position: relative;
          border: 1px solid #dddddd;
          height: 0.9rem;
          line-height: 0.9rem;
          flex: 1;
          .drop-icon {
            position: absolute;
            top: 0px;
            right: 0.267rem;
          }
        }
        .clear-icon {
          position: absolute;
          top: 0.213rem;
          right: 0.67rem;
          font-size: 0.32rem;
          color: #cdcdcd;
        }
      }
    }
  }
  .foot {
    position: absolute;
    bottom: 0px;
    background-color: #fff;

    width: 100%;
    display: flex;

    .foot-btn {
      flex: 1;
      color: #ffffff;
      text-align: center;
      padding: 0.347rem 0;
      font-size: 0.427rem;
    }
    .cancel {
      color: #333333;
    }
    .confirm {
      color: #ffffff;
      background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    }
  }
}
</style>



