<template>
  <div class="foot-tab">
    <div class="tab-box" v-for="(item,index) in tabs" :key="index">
      <router-link :to="item.linkName">
        <div class="iconfont" :class="[(linkName == item.linkName)?item.activeIcon:item.icon]"></div>
        <div
          class="tab-title"
          :class="[(linkName == item.linkName)?'tab-title-active':'']"
        >{{item.name}}</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",

  data() {
    return {
      linkName: null,
      tabs: [
        {
          name: "床位",
          icon: "icon-chuangwei",
          activeIcon: "icon-chuangwei1 activeIcon",
          linkName: "foodIndex"
        },
        {
          name: "订单管理",
          icon: "icon-dingdan1",
          activeIcon: "icon-dingdan2 activeIcon",
          linkName: "foodOrderManage"
        },
        {
          name: "设置",
          icon: "icon-shezhi2",
          activeIcon: "icon-shezhi3 activeIcon",
          linkName: "foodSet"
        },
        {
          name: "个人中心",
          icon: "icon-gerenzhongxin",
          activeIcon: "icon-gerenzhongxin1 activeIcon",
          linkName: "foodHome"
        }
      ]
    };
  },
  created() {
    this.linkName = this.$route.name;
  },
  mounted() {}
};
</script>
<style lang='less'>
.foot-tab {
  position: fixed;
  bottom: 0;
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: #fff;
  color: #999;
  .tab-box {
    width: 20%;

    .iconfont {
      font-size: 0.533rem;
      color: #999;
    }
    .activeIcon {
      color: #f6a129;
    }
    .tab-title {
      margin-top: -0.16rem;
      color: #999;
      text-align: center;
      line-height: 0.533rem;
      font-size: 11px;
    }
    .tab-title-active {
      color: #f6a129;
    }
  }
}
</style>



