<template>
<div class="foodOrderManage">
    <div class="header">
        <div class="tp-search">
            <div class="tp-search-box">
                <div class="iconfont icon-sousuo"></div>
                <input
            type="text"
            v-model="tableNo"
            class="inputCla"
            @change="searchFun()"
            placeholder="输入房号搜索"
          >
                <div class="iconfont icon-quxiao" @click="deletInput" v-show="showDelet"></div>
            </div>
            <div class="tp-right">
                <span class="search-btn" @click="fitleFun()">筛选</span>
            </div>
        </div>
    </div>
    <div class="body">
       <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" :topDistance="40" :maxDistance="100" :top-status.sync="topStatus" ref="loadmore">
        <div class="menu-list" v-if="orderArr.length>0">
           
                <div class="menu-item" v-for="(item, index) in orderArr" :key="index" @click="lookOrderDetails(item)">
                    <div class="num">{{index+1}}</div>
                    <div class="bed-number">
                        <div class="bed">{{item.tableNo}}</div>
                        <div class="number">{{item.accnt}}</div>
                    </div>
                    <div class="date-time">
                        <div class="date">{{item.orderTime|dateFormat}}</div>
                        <div class="time">{{item.orderTime|tiemFormat}}</div>
                    </div>
                    <div class="status failure" v-if="item.orderSta==-1">下单失败</div>
                    <div class="status waitPay" v-else-if="item.orderSta==1">待支付</div>
                    <div class="status cooked" v-else-if="item.orderSta==2">已下厨</div>
                    <div class="status not-cooking" v-else-if="item.orderSta==0">未下厨</div>
                    <i class="iconfont icon-jiantou1"></i>
                </div>
             
        </div>
          <div v-else-if="orderArr.length==0 && this.isShowNotResult" class="no-list">
            <div class="no-result-icon">
                <i class="iconfont icon-jiazaishibai"></i>
            </div>
            <p class="no-result-text">未查到结果</p>
        </div>
           <div slot="top" class="mint-loadmore-top" :class="{ 'rotop': topStatus === 'drop' }">
                    <span v-show="topStatus !== 'loading'"
              :class="{ 'rotate': topStatus === 'drop' }"> ↓ 下拉刷新</span>
                    <span v-show="topStatus === 'loading'">Loading...</span>
                </div>
            </mt-loadmore>
      
    </div>
    <div class="foot"></div>
    <footTabs></footTabs>
    <transition name="OrderFilter">
        <foodOrderFilter class="OrderFilterCon" v-show="isShowOrderFilter" @hidePage="hidePage()" @fitlerSet="fitlerSet($event)"></foodOrderFilter>
    </transition>
</div>
</template>

<script>
import footTabs from "../common/footTabs";
import foodOrderFilter from "../common/foodOrderFilter";
import {
    Indicator,
    Toast
} from "mint-ui";
export default {
    components: {
        foodOrderFilter,
        footTabs
    },
    data() {
        return {
            topStatus: "",
            tableNo: "",
            showDelet: false,
            orderArr: [],
            isShowOrderFilter: false,
            debounceTask: 500,
            status: "",
            createTime: "",
            dinnerTime: "",
            isShowNotResult: false
        };
    },
    created() {
        this.serachData("");
    },
    methods: {
        //input删除键
        deletInput() {
            this.tableNo = "";
            this.serachData("");
        },
        searchFun() {
            if (this.debounceTask !== undefined) clearTimeout(this.debounceTask);
            this.debounceTask = setTimeout(() => {
                this.serachData(this.tableNo);
            }, 500);
        },
        fitleFun() {
            this.isShowOrderFilter = true;
        },
        hidePage() {
            this.isShowOrderFilter = false;
        },
        fitlerSet(val) {
            console.log("val", val);
            this.createTime = val.OrderDate;
            this.dinnerTime = val.EatTime;
            if (val.activeStatus == -2) {
                this.status = "";
            } else {
                this.status = val.activeStatus;
            }
            this.isShowOrderFilter = false;
            this.serachData("");
        },
        lookOrderDetails(item) {
            this.$store.commit("submitOrderNum", {
                orderNum: item.accnt,
                from: "OrderManage"
            });
            this.$router.push("/foodOrder");
        },
        serachData(val) {
            this.isShowNotResult = false;
            let foodUserInfo = JSON.parse(localStorage.foodUserInfo);
            this.$http
                .get("/ohotel/router", {
                    params: {
                        method: "getMasterList",
                        tableNo: val,
                        sta: this.status,
                        createTime: this.createTime,
                        dinnerTime: this.dinnerTime,
                        workStationCode: foodUserInfo.workStationCode
                    },
                    vv: 5
                })
                .then(res => {
                    console.log("liebiao:", res);
                    if (res.data.code == "0") {
                        this.orderArr = res.data.data;
                        this.isShowNotResult = true;
                    } else {
                        this.msg(res.data.msg);
                    }
                });
        },
        loadTop() {
            // 加载更多数据
            this.serachData(this.tableNo);
            this.$refs.loadmore.onTopLoaded();
        },
        handleTopChange: function (status) {
            this.topStatus = status;
        },

        msg(msg) {
            Toast({
                message: msg,
                position: "bottom",
                duration: 3000
            });
        }
    },
    watch: {
        tableNo() {
            if (this.tableNo) {
                this.showDelet = true;
            } else {
                this.showDelet = false;
            }
        }
    },
    filters: {
        dateFormat(val) {
            return val.substring(5, 7) + "/" + val.substring(8, 10);
        },
        tiemFormat(val) {
            return val.substring(11, 16);
        }
    }
};
</script>

<style lang="less" scoped>
.foodOrderManage {
    .header {
        position: fixed;
        top: 0;
        z-index: 1;
        width: 100%;
        box-sizing: border-box;
        padding-left: 0.347rem;
        padding-right: 11px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #393c41;

        .goBack-icon {
            color: #fff;
        }

        .tp-search {
            flex: 1;
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            background-color: #393c41;

            .tp-search-box {
                flex: 1;
                height: 0.853rem;
                margin-top: 0.211rem;
                margin-bottom: 0.211rem;
                display: flex;
                justify-content: flex-start;
                flex-direction: row;
                align-items: center;
                background: #f6f8f9;
                border-radius: 1.3rem;

                .icon-sousuo {
                    color: #cccccc;
                    margin-left: 0.27rem;
                    font-size: 0.37rem;
                }

                .icon-quxiao {
                    color: #cccccc;
                    font-size: 0.32rem;
                    margin-right: 0.27rem;
                }

                .inputCla {
                    border: none;
                    background: #f6f8f9;
                    outline: none;
                    font-size: 0.32rem;
                    width: 85%;
                    margin-left: 0.27rem;
                }
            }

            .tp-right {
                margin-left: 0.4rem;

                .search-btn {
                    font-size: 0.427rem;
                    line-height: 3;
                    color: #fff;
                }
            }
        }
    }

    .body {
        .menu-list {
            padding: 0 0.133rem 1.067rem 0.133rem;
            margin-top: 1.28rem;
min-height: 84vh;
            .menu-item {
                border-radius: 0.133rem;
                background-color: #fff;
                margin-top: 0.133rem;
                padding: 7px 0.267rem;
                display: flex;
                align-items: center;
                text-align: left;

                .num {
                    font-size: 0.373rem;
                    color: #77a0ff;
                    width: 10%;
                }

                .iconfont {
                    width: 5%;
                }

                .bed-number {
                    width: 50%;

                    .bed {
                        font-size: 0.427rem;
                        color: #333333;
                    }

                    .number {
                        font-size: 0.373rem;
                        color: #606a84;
                    }
                }

                .date-time {
                    width: 30%;

                    .date,
                    .time {
                        font-size: 0.373rem;
                        color: #77a0ff;
                    }
                }

                .status {
                    border-radius: 3px;
                    font-size: 0.32rem;
                    width: 60px;
                    height: 0.587rem;
                    line-height: 0.587rem;
                    margin-right: 0.213rem;
                    text-align: center;
                }

                .waitPay {
                    background: #dbebff;
                    color: #467cbb;
                }

                .cooked {
                    color: #ffffff;
                    background: #70a338;
                }

                .failure {
                    background: #ffdbdf;
                    color: #ff2233;
                }

                .not-cooking {
                    color: #333333;
                    background: #f3d824;
                }
            }
        }

        .no-list {
            font-size: 0.32rem;

            .no-result-icon {
                text-align: center;
                padding-top: 2.667rem;

                .iconfont {
                    font-size: 1.28rem;
                }
            }

            .no-result-text {
                text-align: center;
            }
        }
    }

    .OrderFilterCon {
        position: fixed;
        top: 0px;
        left: 0px;
        z-index: 9;
    }

    .OrderFilter-enter-active,
    .OrderFilter-leave-active {
        transition: all 0.5s;
    }

    .OrderFilter-enter,
    .OrderFilter-leave-to {
        margin-left: 100%;
    }
       .rotop {padding-top:50px;}
}
</style>



